@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@700&family=Titan+One&display=swap";
@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&family=Titan+One&display=swap";
:root {
  --products-title-font-family: "Titan One", sans-serif;
  --products-text-font-family: "DM Sans", sans-serif;
  --container-bg-color: #fff;
  --h1-title-color: #d41443;
  --h2-products-color: #fff;
  --text-color: #1e1c1c;
  --products-card-bg-color: #ffb8ca;
  --item-modified-color: #fff;
  --item-text-color: #fff;
  --container-label-color: #ffa5ba;
  --button-bg-color: #ffb8ca;
  --modal-content-bg: #a5a2db;
  --modal-form-text-color: #212121;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.container {
  width: 100%;
  background-color: var(--container-bg-color);
  padding: 60px 20px;
}

.container__label {
  font-family: var(--products-title-font-family);
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--container-label-color);
  padding-top: 61px;
  font-size: 30px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 1280px;
  }
}

body {
  font-family: var(--products-text-font-family);
  color: var(--text-color);
  font-style: normal;
  font-weight: 400;
  line-height: 1.13;
}

h1 {
  font-family: var(--products-title-font-family);
  text-align: center;
  letter-spacing: .06em;
  text-transform: uppercase;
  color: var(--h1-title-color);
  padding-top: 16px;
  padding-bottom: 20px;
  font-size: 58px;
}

h2 {
  font-family: var(--products-title-font-family);
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--h2-products-color);
  font-size: 30px;
}

:root {
  --text-font-family: "DM Sans", sans-serif;
  --text-title-hero: "Titan One", cursive;
  --text-color-light: #fff;
  --transition-effect: 250ms cubic-bezier(.4, 0, .2, 1);
}

