.modal {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  transition: transform var(--transition);
  z-index: 11;
  background: rgba(25, 28, 38, .2);
  justify-content: center;
  align-items: center;
  display: flex;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  position: absolute;
}

.modal-content {
  width: 50%;
  z-index: 11;
  background: #fff;
  margin: 0 auto;
  padding: 16px;
  position: absolute;
  top: 100px;
}

.close-btn {
  color: #d41443;
  cursor: pointer;
  background: #fff;
  border: 1px solid #d41443;
  border-radius: 50%;
  outline: none;
  position: absolute;
  top: 16px;
  right: 16px;
}

.close-btn:hover, .close-btn:focus {
  color: #fff;
  background: #d41443;
}

.form--modal {
  margin-top: 0;
}

.form__title {
