  margin-bottom: 26px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.15;
}

.form__group {
  flex-direction: column;
  display: flex;
}

.form__group > :nth-child(1) {
  letter-spacing: .01em;
  color: #757575;
  font-family: DM Sans;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16;
}

.form__group-options {
  flex-direction: row;
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.form__group-options > :nth-child(1) {
  letter-spacing: .01em;
  color: #757575;
  font-family: DM Sans;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16;
}

.form__group-option {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.form__label {
  letter-spacing: .01em;
  color: #757575;
  font-family: DM Sans;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16;
}

.form__input {
  width: 100%;
  height: 40px;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 40px;
}

.form__input:hover, .form__input:focus {
  fill: #d41443;
  transition: border-color var(--transition-effect), fill var(--transition-effect), outline var(--transition-effect);
  border-color: #d41443;
  outline: none;
}

.form__input:focus ~ .form__icon, .form__input:hover ~ .form__icon {
  fill: #d41443;
  transition: border-color var(--transition-effect), border-color var(--transition-effect);
  border-color: #d41443;
}

.form__input--date {
  width: 100%;
  height: 40px;
  color: #757575;
  border: none;
  border-radius: 4px;
  padding-top: 14px;
  padding-bottom: 14px;
}

.form__placeholder {
  color: #757575;
}

.form__placeholder:focus, .form__placeholder:hover {
  transition: border-color var(--transition-effect);
  border-color: #d41443;
}

.form__input--checkmark:focus, .form__input--checkmark:checked:focus {
  cursor: pointer;
  outline: 1px solid #d41443;
}

.form-controls {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.form__icon {
  width: 18px;
  height: 18px;
  margin-left: 12px;
  position: relative;
  top: -32px;
}

.button--from-modal {
